*{
    border: none;
    text-decoration: none;
}
.navbar {
    position: absolute;
    left: 0;
    top: 0;
    padding: 0;
    margin: 0;width: 100%;
    height: 50px;
    background-color: black;
    border: 5px solid black;
    color: white;   
    display: inline-flex;
    align-content: flex-end;
}
.navbar li{
    list-style-type: none;;
}

.box1{
    background-color: black;
    width: 500px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 20%;
    left: 32%;
    
}
.content p{
    color: red;
    margin: 0;
    font-size: 12px;
    position: relative;
    left: 90px;
}
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.title{
    color: green;
    display: flex;
    
}
.underline{
    width: 50px;
    height: 5px;
    border-radius: 5px;
    background: green;
    position: relative;
    bottom: 10px;
}

.content{
    display: flex;
    
}
.firstname{
   width: 145px;
}
.surname{
    margin-left: 25px;
    width: 145px;
}


.content input{
    margin-left: 90px;
    background: transparent;
    border-radius: 3px;
    margin-top: 10px;
    height: 30px;
    width: 300px;
    border: 2px solid green;
    color: white;
}
 input::placeholder{
    color:whitesmoke
}
.name{
    display: flex;
    
    
}
.name input{
    width: 130px;
}
.dob input[type=date]{
    background-image: url(/src/components/assets/calendar.png);
    background-size: 25px;
    background-repeat: no-repeat;
    background-position: 99%;
}
input[type="date"]::before{
    color: white;
    content: attr(placeholder) ": ";
}
input[type="date"]:focus::before {
    content: "" !important;
}
.address3{
    display: flex;
    justify-content: space-between;
  
}

.state input{
    width: 130px;
}
.pin p{
    position: relative;
    left: 33px;
}
.pin input{
    width: 130px;
    position: relative;
    right: 57px;
}
.already p{
    color: green;
    font-size: 12px;
    margin-left: 90px;
}
.already a{
    font-size: 15px;
    margin-left: 10px;
}


.buttons{
   display: flex;
   justify-content: center;

}
.buttons button{
    align-self: center;
    margin: 20px;
    height: 35px;
    width: 100px;
    position: relative;
    left: 22px;
    color: white;
    background-color: green;
    cursor: pointer;
}
    
    
    